  methods: {
    //shows sidebar by changing sidebar data that is parameter for sidebar component
    showSidebar(data){
      this.sidebar.display = data;
      this.sidebar.visible = true;
    },
    async getYahooData(symbol){
      return await getJSON(
        this.apiUrlBase,
        'api/yahoo',
        {ticker: symbol},
        {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
            },
            //ticker: symbol,
        },
      );
    },
    async deleteStock(ticker){
      let newData = []
      for(let i = 0; i < this.stockData.length; i = i + 1){
        newData[i] = this.stockData[i].ticker;
      }
      newData = newData.filter(stock => stock !== ticker);
      await getJSON(
        this.apiUrlBase,
        'api/userData',
        null,
        {
            method: 'PUT',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({ newData }),
        },
      );
      await this.refreshData();
    },
    //recreates array to push to server, then refreshes by getting the new data from the server
    async addStock(ticker){
      let newData = []
      
      for(let i = 0; i < this.stockData.length; i = i + 1){
        newData[i] = this.stockData[i].ticker;
      }
      newData[newData.length] = ticker;
      await getJSON(
        this.apiUrlBase,
        'api/userData',
        null,
        {
            method: 'PUT',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({ newData }),
        },
      );
      await this.refreshData();
    },
    toCustomizeState(){
      this.state = 'customize';
    },
    toHomeState(){
      this.filteredData = this.stockData;
      this.state = 'home';
    },
    async getStockQuery(){
        this.state = 'stockPage';
        //let response = await fetch('api/yahoo?symbol=' + this.stockQuery);
        let response = await this.getYahooData(this.stockQuery);
        this.stockQueryData = response;//await response.json();
        //this.stockQueryData = this.example_data;
    },
    async refreshData(){

      this.stockData.splice(0);

     let incomingData = await getJSON(
          this.apiUrlBase,
          'api/userStocks',
          null,
          {
              method: 'GET',
              headers: {
                  'Content-Type': 'application/json',
              },
              //body: JSON.stringify({ newData }),
          },
        ); 
      let thisUserData = incomingData.stocks;
      let tickers = Object.values(thisUserData);
      console.log(tickers);
      for(let i = 0; i < tickers.length; i = i + 1){ //uncomment first two lines of loop and comment the exampleData to have api used
        //let response = await fetch('api/yahoo?symbol=' + tickers[i]);
        let response = await this.getYahooData(tickers[i]);
        let thisData = response; //await response.json();
        //let thisData = this.example_data;
        // if(i === 1){
        //   thisData = this.example_data2;
        // }       
        this.$set(this.stockData, i, thisData)
      }
      console.log(this.stockData);
      this.filteredData = this.stockData;
    },
    // helper for calling /api/user
    async refreshMe() {
      //this.user = await fetch('api/user');
      this.user = await getJSON(
        this.apiUrlBase,
        'api/user',
        null,
        {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
            },
        },
      );
      this.userJSON = this.user;//await this.user.json();
      console.log(this.userJSON);
    },
    async refreshAll() {
      await this.refreshMe();
    },
    async sendUpdatedData(data) {
      let newData = []
      for(let i = 0; i < data.length; i = i + 1){
        newData[i] = data[i].ticker;
      }
      await getJSON(
        this.apiUrlBase,
        'api/userData',
        null,
        {
            method: 'PUT',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({ newData }),
        },
      );
      await this.refreshAll();
    },
    async userExists(){
      console.log("checking if user exists");
      return await getJSON(
          this.apiUrlBase,
          'api/doesUserExist',
          null,
          {
              method: 'GET',
              headers: {
                  'Content-Type': 'application/json',
              },
          },
        );
    },
    async createUser() { //initialized with some example data
      const userExists = await this.userExists();
      console.log("Does user exist?");
      console.log(userExists);
      if(!userExists){
        await getJSON(
          this.apiUrlBase,
          'api/createUser',
          null,
          {
              method: 'POST',
              headers: {
                  'Content-Type': 'application/json',
              },
              //body: JSON.stringify({ newData }),
          },
        ); 
      }        
      await this.refreshAll();
      //location.reload();
    },
  },
  watch: {
    minPrice: {
            handler() {
                this.filteredData = this.stockData.filter((e) => e.price > this.minPrice);
            },
        },
  },
  async mounted () {
    this.createUser();
    //window.location.reload();
    this.minPrice = 0 // initializes the filtered set through watch()
    console.log('here before call to userStocks')
    let incomingData = await getJSON(
          this.apiUrlBase,
          'api/userStocks',
          null,
          {
              method: 'GET',
              headers: {
                  'Content-Type': 'application/json',
              },
          },
        ); 
    console.log(incomingData);
    //let incomingJSON = await incomingData.json();
    let thisUserData = incomingData.stocks;//incomingJSON.users[this.userJSON.id].stocks;
    this.isAdmin = incomingData.profile.adminStatus;//incomingJSON.users[this.userJSON.id].profile.adminStatus;
    console.log(this.thisUserData);

    if(this.isAdmin){ // all data is accessible if it is an administrator
      //this.allUsersData = incomingJSON.users;
      this.allUsersData = await getJSON(
          this.apiUrlBase,
          'api/getFirebaseData',
          null,
          {
              method: 'GET',
              headers: {
                  'Content-Type': 'application/json',
              },
              //body: JSON.stringify({ newData }),
          },
        );
    }

  //
    //let tickers = Object.values(incomingJSON)[0];
    let tickers = Object.values(thisUserData);
    console.log(tickers);

    for(let i = 0; i < tickers.length; i = i + 1){
      //let response = await fetch('api/yahoo?symbol=' + tickers[i]);
      let response = await this.getYahooData(tickers[i]);
      let thisData = response;//await response.json();
      //let thisData = this.example_data;
      // if(i === 1){
      //   thisData = this.example_data2;
      // }
      
      this.$set(this.stockData, i, thisData)
    }
    console.log(this.stockData);
    this.filteredData = this.stockData;
  }
}
</script>

